}

.menu-username {
  display: flex;
  align-items: center;
  padding: 12px;
  width: 100%;

  .circle {
    margin-right: 12px;
    width: 24px;
    height: 24px;
    background-color: #67b044;
    color: white;
    font-size: 16px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    text-transform: uppercase;
  }
}

.circle-thumbnail {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;

  cursor: pointer;
  overflow: hidden;

  -webkit-tap-highlight-color: transparent;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.thumbnail-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: opacity 0.2s ease;
}
